body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding:0;
  margin: 0;
}
.App {
  padding: 20px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #282c34;
  color: white;
}

.App-header {
  font-size: calc(10px + 2vmin);
  height: 10%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

main {
  width: 95%;
  max-height: 80%;
}

/* --------book list component--------- */
.book-list{
  width: 100%;
  height: 90%;
  border: 2px solid white;

}




.book-cards{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: rgb(34, 34, 33)

}

.book-card {
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    justify-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 20px;
    background-color: rgba(255, 250, 250, 0.952);
}

.book-card:hover{
    -webkit-transform: scale(1.01,1.01);
            transform: scale(1.01,1.01);
    box-shadow: 0 2px 14px rgba(140, 224, 245, 0.938), 0 10px 10px rgba(65, 64, 64, 0.966);
}

.book-card-body{
    padding:5px;
}

.book-card-image {
    min-width: 150px;
    max-width: 150px;
    min-height: 230px;
    max-height: 230px;
    Object-fit: fill;
    /* max-width: 40%; */
    border: .5px solid grey;
    margin: 5px;
}

.book-card-title {
    font-size: 2.8vw;
    max-width: 90%;
}

.card-title{
    margin-right: 0px;
}

.book-card-text {
    font-size: 1.5vw;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .book-card {
        min-width: 90%;
        max-width: 90%;
    }

    .book-card-image {
        min-width: 150px;
        max-width: 150px;
        min-height: 230px;
        max-height: 230px;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 600px) and (max-width: 1024px) {
    .book-card {
        max-width: 45%;
        min-width: 45%;
    }
    .book-card-title{
        font-size: 2.3vw;
    }
}

/* Large devices (laptops/desktops, 1025px and up) */
/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1025px) {
    .book-card {
        min-width: 31%;
        max-width:31%;
    }

    .book-card-title{
        font-size: 1.5vw;
    }
    .book-card-text{
        font-size: 1vw;
    }
}

